import React, { useReducer } from 'react';
import { useEffect, useState } from 'react';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
} from "react-router-dom";
import { Bar, Line, defaults } from 'react-chartjs-2';
import Chart from 'chart.js';





function Charts() {

    const [stockInfo, updatestock] = useState([]);
    const [dateInfo, updateDates] = useState([]);
    const [returnObj, updateObj] = useState('null');
    const [errorMessage, assertError] = useState('');

    return (
        <div>

            <Link to="/"> Return to Home</Link>


            <div>
                <br />
                <br />
                <ChartSearch />
            </div>
            <ChartView />
            <StockData />
            <InsertError />
           
        </div>



    )


    function ChartSearch() {

       

        const [formStock, updateForm] = useState('');

        return (
            <div className="APISearch">
                <span> Search for ticker: </span><span><input className='formInput' onChange={evt => updateForm(evt.target.value)} type='text'></input></span> <button onClick={buttonClick} id="addButton">Confirm</button>

            </div>

        )

        async function buttonClick() {


            let dateArr = [];

            let priceArr = [];


            await fetch(`https://apidojo-yahoo-finance-v1.p.rapidapi.com/stock/v2/get-chart?interval=1d&symbol=${formStock}&range=1mo&region=US`, {
                "method": "GET",
                "headers": {
                    "x-rapidapi-key": "735c17eaf5msh6b7afe7f835a766p1bfb18jsn91f7d4ad1f8e",
                    "x-rapidapi-host": "apidojo-yahoo-finance-v1.p.rapidapi.com"
                }
            })
                .then((response) => {
                    if (response.status === 400 || response.status === 404 || response.status === 500){
                        console.log('err');
                        assertError('Stock does not exist.');
                        return;
                    }
                    return response.json();
                }
                ).then((result) => {

                    if (result.chart.result === null){
                        assertError('Stock does not exist.');
                        return;
                    }

                    result.chart.result[0].indicators.quote[0].open.forEach(element => {
                        priceArr.push(element);
                    });

                    console.log(result.chart.result[0].timestamp);
                    result.chart.result[0].timestamp.forEach((element) => {

                        let date = new Date(element * 1000);

                        dateArr.push(`${date.getMonth() + 1}/${date.getDate()}`);

                    })

                    updatestock(priceArr);
                    updateDates(dateArr);


                    async function fetchData() {
                        await fetch(`https://apidojo-yahoo-finance-v1.p.rapidapi.com/stock/v2/get-summary?symbol=${formStock}&region=US`, {
                            "method": "GET",
                            "headers": {
                                "x-rapidapi-key": "735c17eaf5msh6b7afe7f835a766p1bfb18jsn91f7d4ad1f8e",
                                "x-rapidapi-host": "apidojo-yahoo-finance-v1.p.rapidapi.com"
                            }
                        })
                            .then((response) => {
                                return response.json();
                            }
                            ).then((result) => {

                                updateObj(result);

                            })
                            .catch(err => {
                                console.error(err);
                            });
                    }

                    fetchData();


                    return;

                })
                .catch(err => {
                    console.error(err);
                });


            let obj = {
                name: formStock,
                score: 5,
                postive_score: 5,
                negative_score: -2,
                activity: 300,

            }


            return;
        }


    }

    function ChartView() {

        if (stockInfo.length === 0) {
            return null;
        }

        console.log(stockInfo === []);

        const state = {
            labels: dateInfo,
            datasets: [
                {
                    label: 'Stock Price',
                    backgroundColor: 'rgba(53,122,14,1)',
                    borderColor: 'rgba(0,0,0,1)',
                    borderWidth: 2,
                    data: stockInfo
                }
            ]
        }

        defaults.borderColor = 'green';
        defaults.color = 'rgb(81, 243, 81)';
        

        return (
            <div>
                <div>
                    <Line
                        data={state}
                        options={{
                            responsive: true,
                            maintainAspectRatio: false,
                            layout: {
                                padding: {
                                    top: 5,
                                    left: 15,
                                    right: 25,
                                    bottom: 15
                                }
                            },
                            title: {
                                display: true,
                                text: 'Average Rainfall per month',
                                fontSize: 20
                            },
                            legend: {
                                display: true,
                                position: 'right'
                            }
                        }}
                        width={10}
                        height={300}

                    />
                </div>


            </div>
        )
    }

    function StockData() {



        if (returnObj === 'null') {
            return null;
        }

        let dividend;



        try {
            dividend = returnObj.summaryDetail.dividendRate.raw;
            if (dividend === undefined) {
                return (
                    <div className='SotD'>
                        <table className="chartTable">
                            <tbody>

                                <tr>
                                    <th><p>Company Name:</p></th>
                                    <td><p>{returnObj.quoteType.shortName}</p></td>

                                    <th><p>52 Week Movement:</p></th>
                                    <td><p>{returnObj.defaultKeyStatistics["52WeekChange"].fmt}</p></td>
                                    <th><p>3 Month Average Volume:</p></th>
                                    <td><p>{returnObj.price.averageDailyVolume3Month.fmt}</p></td>

                                </tr>

                                <tr>
                                    <th><p>Recent Open:</p></th>
                                    <td><p>${Math.round(100 * returnObj.price.regularMarketOpen.raw)/100}</p></td>
                                    <th><p>Quarterly Growth:</p></th>
                                    <td><p>{returnObj.defaultKeyStatistics.earningsQuarterlyGrowth.fmt}</p></td>
                                </tr>

                                <tr>
                                    <th><p>Dividend Rate:</p></th>
                                    <td><p>None</p></td>
                                    <th><p>Forward PE:</p></th>
                                    <td><p>{Math.round(100*returnObj.defaultKeyStatistics.forwardPE.raw)/100}x</p></td>


                                </tr>

                            </tbody>

                        </table>
                    </div>
                )
            }
            return (

                <div className='SotD'>
                    <table className = "chartTable">
                        <tbody>

                            <tr>
                                <th><p>Company Name:</p></th>
                                <td><p>{returnObj.quoteType.shortName}</p></td>

                                <th><p>52 Week Movement:</p></th>
                                <td><p>{returnObj.defaultKeyStatistics["52WeekChange"].fmt}</p></td>
                                <th><p>3 Month Average Volume:</p></th>
                                <td><p>{returnObj.price.averageDailyVolume3Month.fmt}</p></td>

                            </tr>

                            <tr>
                                <th><p>Recent Open:</p></th>
                                <td><p>${Math.round(100 * returnObj.price.regularMarketOpen.raw)/100}</p></td>
                                <th><p>Quarterly Growth:</p></th>
                                <td><p>{returnObj.defaultKeyStatistics.earningsQuarterlyGrowth.fmt}</p></td>
                            </tr>

                            <tr>
                                <th><p>Dividend Rate:</p></th>
                                <td><p>{dividend}c</p></td>
                                <th><p>Forward PE:</p></th>
                                <td><p>{Math.round(100*returnObj.defaultKeyStatistics.forwardPE.raw)/100}x</p></td>


                            </tr>

                        </tbody>

                    </table>
                </div>
            )

        } catch {
            console.log('no Dividend')
        }

        return;

    }

    function InsertError() {

        if (errorMessage === '') {
            return null;
        }

        setTimeout(() => {
            assertError('');
        }, 1000);

        return (<p id="errorChart">{errorMessage}</p>);

    }


}

export default Charts
