import Startup from './Startup';
import stonkPic from './stonk.jpg';
import calcPic from './calculator.jpg';
import sentimentPic from './SENTIMENT.jpg';
import React, { useReducer } from 'react';
import { useEffect, useState } from 'react';
import MyComponent from './Startup';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
} from "react-router-dom";


function Calculator() {

    const [shares, updateShares] = useState(0);
    const [price, updatePrice] = useState(0);
    const [div, updateDiv] = useState(0);
    const [options, updateOptions] = useState(0);
    const [newPrice, updateNewPrice] = useState(0);
    const [newCost, updateNewCost] = useState(0);
    const [returns, updateReturn] = useState(0);

    let shareInt = parseInt(shares);
  
    let priceFloat = parseFloat(price);
    
    let divFloat = parseFloat(div);
    
    let optionsFloat = parseFloat(options);

    let newPriceFloat = parseFloat(newPrice);

    let newCostBasis = Math.round(100 *((shareInt*priceFloat) - ((divFloat*shareInt) + ((optionsFloat*12) * shareInt)))/shareInt)/100;

    let annualReturn = Math.round( 100*(((shareInt*priceFloat) + (shareInt*optionsFloat*12) + (shareInt*divFloat) + (shareInt * (newPriceFloat - priceFloat)))/(shareInt*priceFloat) - 1)*100)/100;
    if(isNaN(newCostBasis)){
        newCostBasis = 'Waiting...';
    } else {
        newCostBasis = '$' + newCostBasis
    }

    if(isNaN(annualReturn)){
        annualReturn = 'Waiting...';
    } else {
        annualReturn = annualReturn + '%'
    }
   
    return (
        <div>
            <Link to="/"> Return to Home </Link>

            <table id='calculatorTable'>
                <tbody>
                    <tr>
                        <th className='calc'>
                            <p className='calc'>Shares</p>
                        </th>
                        <th className='calc'>
                            <p className='calc'>Avg cost</p>
                        </th>
                        <th className='calc'>
                            <p className='calc'>Annual Dividend(cents/share)</p>
                        </th>
                        <th className='calc'>
                            <p className='calc'>Monthly Option Premium(cents/share)</p>
                        </th>
                        <th className='calc'>
                            <p className='calc'>New share price</p>
                        </th>
                    </tr>
                    <tr>
                        <td className = 'calc'>
                            <input className='calcInput' value={shares} onChange={(e) => {
                                const re = /\d+((\.|,)\d+)?/;

                                // if value is not blank, then test the regex

                                if (e.target.value === '' || re.test(e.target.value)) {
                                    updateShares(e.target.value);
                                }
                            }}></input>
                        </td>
                        <td className = 'calc'>
                            <input className='calcInput' value={price} onChange={(e) => {
                                const re = /\d+((\.|,)\d+)?/;

                                // if value is not blank, then test the regex

                                if (e.target.value === '' || re.test(e.target.value)) {
                                    updatePrice(e.target.value);
                                }
                            }}></input>
                        </td>
                        <td className = 'calc'>
                            <input className='calcInput' value={div} onChange={(e) => {
                                const re = /\d+((\.|,)\d+)?/;

                                // if value is not blank, then test the regex

                                if (e.target.value === '' || re.test(e.target.value)) {
                                    updateDiv(e.target.value)
                                }
                            }}></input>
                        </td>
                        <td className = 'calc'>
                            <input className='calcInput' value={options} onChange={(e) => {
                                const re = /\d+((\.|,)\d+)?/;

                                // if value is not blank, then test the regex

                                if (e.target.value === '' || re.test(e.target.value)) {
                                    updateOptions(e.target.value)
                                }
                            }}></input>
                        </td>
                        <td className = 'calc'>
                            <input className='calcInput' value={newPrice} onChange={(e) => {
                                const re = /\d+((\.|,)\d+)?/;

                                // if value is not blank, then test the regex

                                if (e.target.value === '' || re.test(e.target.value)) {
                                    updateNewPrice(e.target.value)
                                }
                            }}></input>
                        </td>
                    </tr>
                    <tr></tr>
                    <tr>

                        <th className='calc'>
                            <p className='calcF'>New Cost Basis:</p>
                        </th>
                        <th className='calc'>
                            <p className='calcF'>{newCostBasis}</p>
                        </th>
                        
                    </tr>
                    <tr></tr>
                    <tr>
                    <th className='calc'>
                            <p className='calcF'>Annualized Return:</p>
                        </th>
                        <th className='calc'>
                            <p className='calcF'>{annualReturn}</p>
                        </th >
                    </tr>
                </tbody>


            </table>
        </div>
    )





}

export default Calculator