import { useEffect, useState } from 'react';
import React, { useReducer } from 'react';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
} from "react-router-dom";

function CreateUser(){
    const [username, updateUsername] = useState('');
    const [password, updatePassword] = useState('');
    const [errorMessage, assertError] = useState('');
    const [successMessage, assertSuccess] = useState('');

    return(
        <div>
            <Link to="/"> Return to Home</Link> 
            <br/>
            <h1>Create User:</h1>
            <form>
                    <label>
                        <input type="text" name="user" onChange={evt => updateUsername(evt.target.value)} /> <span> <input type="password" name="pass" onChange={evt => updatePassword(evt.target.value)} /></span>
                    </label>
                    <button type="button" id="loginButton" onClick={createButton}> Create </button>
            </form>
            <InsertError />
            <InsertSuccess />
        </div>

    )

    function createButton() {

        let obj = {

            username: username,
            password: password

        }

        let createSuccess = false;

        if (username === '') {
            assertError("Incorrect username or password.");
            return;
        }

        async function fetchData() {
            await fetch('http://99.109.3.109:3000/Users', {
                method: "POST",
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify(obj)

            })
                .then((res) => {

                    if (res.status === 404 || res.status === 500) {
                        assertError("Username already exists.");
                        return;
                    }
                    createSuccess = true;
                    return res.json()
                })
                .then(
                    (result) => {

                        if (!createSuccess) {
                

                            return;
                        } 

                        console.log('success');

                        assertSuccess('Success!');

                        return;

                    },


                ).catch(err => console.log(err))

        }

        fetchData();

        return false;
    }

    function InsertError() {

        if (errorMessage === '') {
            return null;
        }

        setTimeout(() => {
            assertError('');
        }, 1000);

        return (<p  className="createError">{errorMessage}</p>);

    }
    function InsertSuccess() {

        if (successMessage === '') {
            return null;
        }

        setTimeout(() => {
            assertSuccess('');
        }, 1000);

        return (<p className="createError">{successMessage}</p>);

    }


}

export default CreateUser