
import React, { useReducer } from 'react';
import { useEffect, useState } from 'react';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
} from "react-router-dom";
import { Bar, Line, defaults } from 'react-chartjs-2';
import Chart from 'chart.js';





function Sentiment() {

    const [stockInfo, updatestock] = useState([]);
    const [dateInfo, updateDates] = useState([]);
    const [mostRecentStock, updateRecent] = useState({});
    const [errorMessage, assertError] = useState('');

    return (
        <div>
            <Link to="/"> Return to Home</Link>
            <div className='APISearch'>




                <div>
                    <br />
                    <br />
                    <StockSearch />
                </div>
                <SentimentView />
                <InsertError />
            </div>
        </div>


    )


    function StockSearch() {
        const [formStock, updateForm] = useState('');

        return (
            <div>
                <span> Search for ticker: </span><span><input className='formInput' onChange={evt => updateForm(evt.target.value)} type='text'></input></span> <button onClick={buttonClick} id="addButton">Confirm</button>

            </div>

        )

        async function buttonClick() {


            let dateArr = [];

            let scoreArr = [];


            await fetch(`https://socialsentiment-io.p.rapidapi.com/stocks/${formStock.toUpperCase()}/sentiment/daily/`, {
                "method": "GET",
                "headers": {
                    "x-rapidapi-key": "735c17eaf5msh6b7afe7f835a766p1bfb18jsn91f7d4ad1f8e",
                    "x-rapidapi-host": "socialsentiment-io.p.rapidapi.com"
                }
            })
                .then((response) => {

                    console.log(response);
                    if (response.status === 400 || response.status === 404 || response.status === 500){
                        console.log('err');
                        assertError('Stock does not exist.');
                        return;
                    }
                    return response.json()
                }
                ).then((result) => {

                 
                    if(result.length === 0){
                        assertError('Stock does not exist.');
                        return;
                    }

                    result.forEach((resultObj) => {
                        dateArr.push(resultObj.date)
                        scoreArr.push(resultObj.score)
                    })


                    updatestock(scoreArr);
                    updateDates(dateArr);
                    updateRecent(result[7]);
                    

                })
                .catch(err => {
                    console.error(err);
                });


            let obj = {
                name: formStock,
                score: 5,
                postive_score: 5,
                negative_score: -2,
                activity: 300,

            }


            return;
        }


    }

    function SentimentView() {

        if (stockInfo.length === 0) {
            return null;
        }

        const state = {
            labels: dateInfo,
            datasets: [
                {
                    label: 'Sentiment Score',
                    backgroundColor: 'rgba(53,122,14,1)',
                    borderColor: 'rgba(0,0,0,1)',
                    borderWidth: 2,
                    data: stockInfo
                }
            ]
        }

        defaults.borderColor = 'green';
        defaults.color = 'rgb(81, 243, 81)';

        return (
            <div>
                <div>
                    <Bar
                        data={state}
                        options={{
                            responsive: true,
                            maintainAspectRatio: false,
                            layout: {
                                padding: {
                                    top: 5,
                                    left: 15,
                                    right: 25,
                                    bottom: 15
                                }
                            },
                            title: {
                                display: true,
                                text: 'Average Rainfall per month',
                                fontSize: 20
                            },
                            legend: {
                                display: true,
                                position: 'right'
                            }
                        }}
                        width={10}
                        height={300}

                    />
                </div>

                <div id="sentimentDiv">
                    <table id="sentimentTable">

                        <tr>
                            <th className = 'sent'><p className = 'sent'>Most recent date</p></th>
                            <th className = 'sent'><p className = 'sent'>Activity</p></th>
                            <th className = 'sent'><p className = 'sent'>Positive score</p></th>
                            <th className = 'sent'><p className = 'sent'>Negative score</p></th>
                            <th className = 'sent'><p className = 'sent'>7 Day Average</p></th>
                            <th className = 'sent'><p className = 'sent'>30 Day Average</p></th>
                        </tr>
                        <tr>
                            <td className = 'sent'><p className = 'sent'>{mostRecentStock.date}</p></td>
                            <td className = 'sent'><p className = 'sent'>{mostRecentStock.activity}</p></td>
                            <td className = 'sent'><p className = 'sent'>{mostRecentStock.positive_score}</p></td>
                            <td className = 'sent'><p className = 'sent'>{mostRecentStock.negative_score}</p></td>
                            <td className = 'sent'><p className = 'sent'>{mostRecentStock.avg_7_days}</p></td>
                            <td className = 'sent'><p className = 'sent'>{mostRecentStock.avg_30_days}</p></td>


                        </tr>



                    </table>
                </div>
            </div>
        )
    }

    function InsertError() {

        if (errorMessage === '') {
            return null;
        }

        setTimeout(() => {
            assertError('');
        }, 1000);

        return (<p id="errorChart">{errorMessage}</p>);

    }


}

export default Sentiment
