import Startup from './Startup';
import stonkPic from './stonk.jpg';
import calcPic from './calculator.jpg';
import sentimentPic from './SENTIMENT.jpg';
import React, { useReducer } from 'react';
import { useEffect, useState } from 'react';
import MyComponent from './Startup';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
} from "react-router-dom";


function Home() {

    let stockString = "";

    const [loggedIn, updateLogin] = useState(false);

    const [formUpdate, updatePage] = useState('');

    useEffect(() => {
        updatePage();
    }, [formUpdate])


    return (<div className="App">

        <section className="hero">
            <div className="hero-body">
                <div className="container">
                    <h1 className="title textCenter pageHeader">
                        StockFriend
                </h1>
                    <h2 className="subtitle textCenter pageHeader">
                        Tools for the diligent investor
                </h2>
                </div>
            </div>
        </section>

        <LoginComponent />

        <section className="section">
            <div className="container">

                <div className="columns is-gapless">

                    <div className="column">
                        <img id="mainPic" className="roundedCorners" src={stonkPic} alt="stonkChart" />
                        <h1 className="imgHeader has-text-centered"> <Link to="/charts">Stock Charts</Link> </h1>
                    </div>
                    <div className="column">
                        <img src={calcPic} className="roundedCorners" alt="calculator" id="mainPic" />
                        <h1 className="imgHeader has-text-centered"><Link to="/calculator">Calculator tool</Link> </h1>
                    </div>
                    <div className="column">
                        <img src={sentimentPic} className="roundedCorners" alt="Sentiment" id="mainPic" />
                        <h1 className="imgHeader has-text-centered"><Link to="/Sentiment"> Stock Sentiment </Link> </h1>
                    </div>



                </div>
            </div>

        </section>

        <SotD />


    </div>);


    function SotD() {
        if (sessionStorage.getItem('loggedIn') === 'true') {


            return (<section className="section">
                <div className="container">

                    <div className="columns">
                        <div className="column">
                            <h1>My Stocks: </h1>
                            <div id="SotD">

                                <GetStocks />

                            </div>
                            <InsertButton />
                        </div>
                        <div className="column">
                            <h1>Stock Highlight: </h1>
                            <div id="SotD">

                              <Startup /> 

                            </div>
                        </div>
                    </div>


                </div>
            </section>);
        } else {
            return null;
        }
    }

    function GetStocks() {

        const [error, setError] = useState(null);
        const [stocks, setItems] = useState([]);

        
        useEffect(() => {

            async function fetchData() {
                await fetch(`http://99.109.3.109:3000/Stocks/${sessionStorage.getItem('username')}`, {
                    "method": "GET",
                })
                    .then(res => res.json())
                    .then(
                        (result) => {


                            if (result === null) {
                                return;
                            }

                            console.log(result);
                            setItems(result.stockArr)



                        },
                        
                        (error) => {
                            setError(error);
                        }
                    )
            }

            fetchData();

        }, []);

        if (error) {
            return <div>Error: {error.message}</div>;
        } else {
            return <div className="has-text-left SotDEle">

                <table class = 'chartTable'>
                    <tr>
                        <th><p>Ticker</p></th>
                        <th><p>Shares</p></th>
                        <th><p>Average Price</p></th>
                    </tr>

                    {stocks.map(stock => (

                        <tr>
                            <td><p className='capitalized'>{stock.ticker}</p></td>
                            <td><p>{stock.shares}</p></td>
                            <td><p>${stock.price}</p></td>
                        </tr>

                    ))}

                </table>
            </div>

        }


    }


    function InsertButton() {
        const [formStock, updateForm] = useState('');
        const [formShares, updateForm2] = useState(0);
        const [formPrice, updateForm3] = useState(0);
        const [removeStock, updateForm4] = useState('');
        const [errorMessage, assertError] = useState('');

        if (sessionStorage.getItem('loggedIn') === "true") {


            return (
                <div>
                    <div>
                        <span> Add Ticker: </span><span><input className='formInput' onChange={evt => updateForm(evt.target.value)} type='text'></input></span>
                        <span> Shares: </span><span><input className='formInput' onChange={evt => updateForm2(evt.target.value)} type='text'></input></span>
                        <span> Avg Price: </span><span><input className='formInput' onChange={evt => updateForm3(evt.target.value)} type='text'></input></span> <button onClick={buttonClick} id="addButton">Confirm</button>
                        <InsertError />
                    </div>
                    <br />
                    <div id='removeStock'>
                        <span> Remove Ticker: </span><span><input className='formInput' onChange={evt => updateForm4(evt.target.value)} type='text'></input></span>  <button onClick={removeClick} id="removeButton">Confirm</button>
                    </div>
                </div>
            )
        } else {
            return null;
        }
        function buttonClick() {

            let isAStock = true;



            async function fetchData() {

                await fetch(`https://yahoo-finance-low-latency.p.rapidapi.com/v11/finance/quoteSummary/${formStock}?modules=defaultKeyStatistics%2CassetProfile`, {
                    "method": "GET",
                    "headers": {
                        "x-rapidapi-key": "735c17eaf5msh6b7afe7f835a766p1bfb18jsn91f7d4ad1f8e",
                        "x-rapidapi-host": "yahoo-finance-low-latency.p.rapidapi.com"
                    }
                })
                    .then(response => {
                        if (response.status === 404) {
                            isAStock = false;
                            assertError('Stock does not exist.');
                        }
                    })
                    .catch(err => {
                        console.error(err);
                    });



                if (!isAStock) {
                    return;
                }
                console.log(sessionStorage.getItem('username'))
                let obj = {
                    username: sessionStorage.getItem('username'),
                    ticker: formStock.toUpperCase(),
                    shares: formShares,
                    price: formPrice

                }

                await fetch("http://99.109.3.109:3000/Stocks", {
                    method: "POST",
                    headers: { 'Content-Type': 'application/json' },
                    body: JSON.stringify(obj)

                })
                    .then(res => res.json())
                    .then(
                        (result) => {

                            console.log(result);
                            updatePage(result);


                        },
                       

                    )
            }

            fetchData();

            return;
        }

        function removeClick(){

            let obj = {
                ticker: removeStock.toUpperCase()
            }

            fetch(`http://99.109.3.109:3000/Stocks/${sessionStorage.getItem('username')}`, {
                    method: "DELETE",
                    headers: { 'Content-Type': 'application/json' },
                    body: JSON.stringify(obj)

                })
                    .then(res => res.json())
                    .then(
                        (result) => {

                            console.log(result);
                            updatePage(result);


                        },
                       

                    )
        }

        

        function InsertError() {

            if (errorMessage === '') {
                return null;
            }

            setTimeout(() => {
                assertError('');
            }, 1000);

            return (<p>{errorMessage}</p>);

        }
    }

    function LoginComponent() {

        const [username, updateUsername] = useState('');
        const [password, updatePassword] = useState('');
        const [errorMessage, assertError] = useState('');

        if (sessionStorage.getItem('loggedIn') === null) {



            return (

                <div id="login">
                    <span><p id="username">Username:</p> </span> <span><p id="password">Password:</p></span>
                    <form>
                        <label>
                            <input type="text" name="user" onChange={evt => updateUsername(evt.target.value)} /> <span> <input type="password" name="pass" onChange={evt => updatePassword(evt.target.value)} /></span>
                        </label>
                        <button type="button" id="loginButton" onClick={loginButton}> Log In</button>
                    </form>
                    <InsertError />

                    <br/>

                    <div>
                        <Link to="/create"><p id='create'>Create User</p></Link>
                    </div>

                </div>

                

            );

        } else {


            return (


                <div id="loggedIn">

                    <p>Logged in as: {sessionStorage.getItem('username')} &nbsp;&nbsp;&nbsp;&nbsp;&nbsp; <span type="button" id="logout" onClick={logoutButton}>Logout</span></p>

                </div>

            )
        }

        function loginButton() {

            let passObj = {

                password: password

            }

            let loginSuccess = false;

            if (username === '') {
                assertError("Incorrect username or password.");
                return;
            }

            async function fetchData() {
                await fetch(`http://99.109.3.109:3000/Users/${username}`, {
                    method: "POST",
                    headers: { 'Content-Type': 'application/json' },
                    body: JSON.stringify(passObj)

                })
                    .then((res) => {

                        if (res.status === 404) {
                            console.log(404);
                            return;
                        }
                        loginSuccess = true;
                        return res.json()
                    })
                    .then(
                        (result) => {

                            if (!loginSuccess) {
                                assertError("Incorrect username or password.");

                                return;
                            }

                            sessionStorage.setItem('username', username);
                            sessionStorage.setItem('loggedIn', true);
                            updateLogin(true);

                            return;

                        },


                    )

            }

            fetchData();

            return false;
        }

        function logoutButton() {


            sessionStorage.removeItem('username');
            sessionStorage.removeItem('loggedIn');
            updateLogin('');

            return;

        }

        function InsertError() {

            if (errorMessage === '') {
                return null;
            }

            setTimeout(() => {
                assertError('');
            }, 1000);

            return (<p id="error">{errorMessage}</p>);

        }

    }



}







export default Home;