import { useEffect, useState } from 'react';


function MyComponent() {
    const [error, setError] = useState(null);
    const [item, setItem] = useState('');
    const [itemArr, setArr] = useState([]);

    // Note: the empty deps array [] means
    // this useEffect will run once
    // similar to componentDidMount()

    if (item === '') {
        fetch(`http://99.109.3.109:3000/Stocks/${sessionStorage.getItem('username')}`, {
            "method": "GET",
        })
            .then(res => res.json())
            .then(
                (result) => {


                    try {

                        setItem(result.stockArr[Math.floor(Math.random() * result.stockArr.length)].ticker);
                        
                    } catch {
                        console.log("No tickers")
                    }


                    return;
                },
                // Note: it's important to handle errors here
                // instead of a catch() block so that we don't swallow
                // exceptions from actual bugs in components.
                (error) => {
                    setError(error);
                }
            )
    }

    if (item === '') {
        return null;
    }

   

    if (itemArr.length === 0 && sessionStorage.getItem('reqSent') !== 'true') {

        sessionStorage.setItem('reqSent', 'true');

        fetch(`https://yahoo-finance-low-latency.p.rapidapi.com/v11/finance/quoteSummary/${item}?modules=defaultKeyStatistics%2CassetProfile`, {
            "method": "GET",
            "headers": {
                "x-rapidapi-key": "735c17eaf5msh6b7afe7f835a766p1bfb18jsn91f7d4ad1f8e",
                "x-rapidapi-host": "yahoo-finance-low-latency.p.rapidapi.com"
            }
        })
            .then(res => res.json())
            .then(
                (result) => {

                    if(result.quoteSummary === undefined || result.quoteSummary.result === null){
                        sessionStorage.setItem('reqSent', 'false');
                        return;
                    }

                    let info = [];
               

                    info.push(item);
                    info.push(result.quoteSummary.result[0].assetProfile.city);
                    info.push(result.quoteSummary.result[0].assetProfile.industry);
                    info.push(result.quoteSummary.result[0].assetProfile.fullTimeEmployees);
                    info.push(result.quoteSummary.result[0].assetProfile.website);
                    info.push(result.quoteSummary.result[0].defaultKeyStatistics.forwardPE.fmt);
                    info.push(result.quoteSummary.result[0].defaultKeyStatistics.profitMargins.fmt);

                    setArr(info);


                    sessionStorage.setItem('reqSent', false);


                },
                // Note: it's important to handle errors here
                // instead of a catch() block so that we don't swallow
                // exceptions from actual bugs in components.
                (error) => {
                    setError(error);
                }
            )
    }

    if (error) {
        return <div>Error: {error.message}</div>;
    } else if (itemArr.length > 0) {
        return (

            <div className = "SotDEle">
                <p>Company symbol: &nbsp;&nbsp;{itemArr[0]}</p> 
                <p>Based in: &nbsp;&nbsp;{itemArr[1]}</p>
                <p>Industry: &nbsp;&nbsp;{itemArr[2]}</p>
                <p>Employee Count: &nbsp;&nbsp;{itemArr[3]}</p>
                <br></br>
                <p>Website: &nbsp;&nbsp;{itemArr[4]}</p> 
            </div>
        )

    }

    return (
        <div>
            <p>Loading...</p>

            <p>If stock doesn't load, ticker may have changed. Refresh for a new one.</p>
        </div>
    )

}




export default MyComponent;

